.divContainerAbout{display:flex;justify-content:space-around;align-items:center;padding-bottom:50px;max-width:100vw;padding-top:50px}.divContainerAbout div{height:30%;width:50%;text-align:justify;display:flex;flex-direction:column}.divContainerAbout div h2{font-family:"Roboto",sans-serif;font-weight:700;font-size:30px}.divContainerAbout div p{font-family:"Roboto",sans-serif;font-style:italic;font-weight:400}.divContainerAbout div ul{margin-top:15px;margin-bottom:15px;display:grid;grid-template-columns:repeat(2, 1fr);grid-template-rows:repeat(3, 1fr);grid-column-gap:20px;grid-row-gap:2px}.divContainerAbout div ul li{font-family:"Roboto",sans-serif;font-weight:400}.divContainerAbout div ul li::before{content:"âˆ´";margin-right:5px;color:gray}.divContainerAbout div a{margin:auto;padding-top:20px}.divContainerAbout div a button{padding:1rem 2rem;border-radius:6px;font-family:"Roboto",sans-serif;font-weight:400;justify-self:center;align-self:center}.divContainerAbout img{width:20%;height:30%;border-radius:15px}@media(max-width: 426px){.divContainerAbout{display:flex;flex-direction:column}.divContainerAbout div{width:90%}.divContainerAbout div a{width:100%}.divContainerAbout div a button{width:100%}.divContainerAbout img{width:80%;margin-bottom:15px}}@media(max-width: 768px)and (min-width: 426px){.divContainerAbout{display:flex;flex-direction:column}.divContainerAbout div{width:90%}.divContainerAbout div a{width:100%}.divContainerAbout div a button{width:100%}.divContainerAbout img{width:80%;margin-bottom:15px}}